.how-it-works-container {
  position: absolute;
  top: 50%;
  right: 0%;
  transform: translate(-20%, -2%);
  padding: 20px;
  background-color: #0000002a;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  margin: -100px auto 0;
  max-width: 70%;
  color: #333;
}

.images-container {
  display: flex;
  justify-content: center;
  align-items: stretch; 
  gap: 20px;
  flex-wrap: wrap;
}

.image-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  padding: 20px;
  align-self: stretch; 
}

img {
  width: 60%;
  max-width: 500px;
  height: auto;
  border-radius: 8px;
}

p {
  color: #302d1f;
  font-size: 18px;
  line-height: 2;
  margin: 10px 0;
}

@media (max-width: 768px) {
  .images-container {
    position:absolute;
    flex-direction: column;
  }

  img {
    width: 70%;
  }

  .how-it-works-container {
    padding: 5px;
  }
}
