.petal-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
}

.petal {
  position: absolute;
  width: 50px;
  height: 50px; 
  background-size: cover;
  opacity: 0.8;
  animation: fall 10s linear infinite;
}

.petal1 {
  background-image: url('./petals/petal1.png');
}

.petal2 {
  background-image: url('./petals/petal2.png');
}

.petal3 {
  background-image: url('./petals/petal3.png');
}

.petal4 {
  background-image: url('./petals/petal4.png');
}

.petal5 {
  background-image: url('./petals/petal5.png');
}

@keyframes fall {
  from {
    transform: translateY(-100vh); 
  }
  to {
    transform: translateY(100vh);
  }
}